<script setup lang="ts">
import { ref } from 'vue'

const modelValue = defineModel()
interface IProps {
  type: string
  label: string
  name: string
  required: boolean
  removeValidationStyles: boolean
}
const {
  type = 'text',
  label = 'Label',
  name = '',
  required = false,
  removeValidationStyles = false,
} = defineProps<IProps>()

const input = ref(null)
const validationMessage = ref('')

const handleBlur = (e: FocusEvent) => {
  if (e.target instanceof HTMLInputElement) {
    validationMessage.value = e.target?.validationMessage
  }
}
</script>

<template>
  <div class="relative">
    <input
      ref="input"
      v-model="modelValue"
      :type="type"
      :name="name"
      class="peer w-full rounded border-2 border-gray-50 bg-gray-50 px-3 py-2 outline-none transition-all duration-100 placeholder:text-transparent focus:border-gray-100"
      :class="{ '[&:not(:focus)]:invalid:border-red-200 [&:not(:focus)]:invalid:text-red-500': !removeValidationStyles }"
      placeholder="."
      :required="required"
      @blur="handleBlur"
    >
    <label
      for="email"
      class="pointer-events-none absolute -top-6 left-[3px] text-sm text-gray-500 transition-all duration-100 peer-placeholder-shown:left-[9px] peer-placeholder-shown:top-[10px] peer-placeholder-shown:text-base peer-focus:-top-6 peer-focus:left-[3px] peer-focus:text-sm"
      :class="{ 'peer-[&:not(:focus)]:peer-invalid:text-red-500': !removeValidationStyles }"
      v-html="label"
    />
    <p
      v-if="validationMessage"
      class="mt-1 text-xs text-red-500"
      v-html="validationMessage"
    />
  </div>
</template>
