<script setup lang="ts">
import { ref } from 'vue'
import { useRouter } from 'vue-router/auto'
import { useAuth } from '@/shared/api/useAuth'
import TextInput from '@/shared/ui/forms/TextInput.vue'
import { useUi } from '@/shared/lib/composables/useUi'
import { useUiStore } from '@/shared/lib/composables/ui'
import AALogo from '@/shared/ui/logos/AALogo.vue'
import VActionButton from '@/shared/ui/forms/VActionButton.vue'

const { attemptLogin, authActions } = useAuth()
const { actionIsInProgress, actionIsInError, validateForm } = useUi()
const { getActionErrorMessage } = useUiStore()

const router = useRouter()
const email = ref('')
const password = ref('')

const loginFormElement = ref<HTMLFormElement | null>(null)
const handleAttemptLogin = async () => {
  await validateForm(
    loginFormElement.value,
    () => attemptLogin(
      email.value,
      password.value,
      router,
    ),
  )
}
</script>

<template>
  <div class="flex h-full items-center justify-center md:bg-gray-100">
    <div class="mt-4 flex w-full max-w-[500px] flex-col items-center justify-center rounded bg-white px-8 py-6 md:shadow-lg">
      <AALogo class="mb-8 w-1/3" />

      <form
        id="login"
        ref="loginFormElement"
        class="w-full"
      >
        <div class="mt-4 flex flex-col space-y-4 md:space-y-8">
          <TextInput
            v-model="email"
            type="email"
            name="email"
            label="Email"
            required
            remove-validation-styles
          />
          <TextInput
            v-model="password"
            type="password"
            name="password"
            label="Password"
            required
            remove-validation-styles
          />

          <span class="w-full text-center">
            <p
              v-if="actionIsInError(authActions.attemptLogin)"
              class="text-sm text-red-600"
              v-html="getActionErrorMessage(authActions.attemptLogin)"
            />
          </span>

          <div class="flex flex-wrap items-baseline md:justify-between">
            <VActionButton
              label="Login"
              :loading="actionIsInProgress(authActions.attemptLogin)"
              :disabled="email === '' || password === ''"
              class="w-full md:w-auto"
              :action="handleAttemptLogin"
            />
            <a
              href="#"
              class="text-sm text-blue-500 hover:underline"
            >
              Forgot password?
            </a>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
